			<view class="uaer_name">
				<view class="_left">
					<text class="_name">{{user_info.name}}</text>
					<text class="signature">{{user_info.sign}}</text>
				</view>
				<view class="_right">
					<image :src="user_info.photo" mode="aspectFill"></image>
					<i class="iconfont">&#xe711;</i>
				</view>
			</view>
			<view class="fans_num">
				<view class="_content">
					<text>1234</text>
					<text>人气</text>
				</view>
				<view class="_content">
					<text>{{user_info.fensi}}</text>
					<text>粉丝</text>
				</view>
				<view class="_content">
					<text>{{user_info.guanzhu}}</text>
					<text>关注</text>
				</view>
			</view>
		</view>
		<view class="_lists" v-for="(n, index) in recogmentList" :key="index">
			<image class="header_img" :src="n.photo" mode="aspectFill" @click="_details"></image>
			<view class="_right">
				<view class="user_name">
					<text>{{n.name}}</text>
					<!-- <text class="guanzhu">+ 关注</text> -->
				</view>
				<text class="text_content">{{n.content}}</text>
				<!-- 图片 -->
				<image v-if="n.imageslist.length < 2 && n.imageslist.length !== 0" class="big_images" :src="n.imageslist[0]" mode="aspectFill"></image>
				<view class="img_content" v-if="n.imageslist.length > 1">
					<image class="images" v-for="(m, index1) in n.imageslist" :key="index1" :src="m" mode="aspectFill"></image>
				</view>
				<text class="time_content">{{n.timeout}}</text>
				<!-- 评论 -->
				<!-- <view class="comment_content">
					<view class="comment_list" v-for="(i, index2) in 3" :key="index2">
						<text class="comment_name">有理想的咸鱼:</text>
						<text>无聊</text>
					</view>
				</view> -->
				<view class="bottom_tab">
					<text>{{n.dianzan}}喜欢</text>
					<text>{{n.pinglun}}评论</text>
					<view class="content_event">
						<i></i>
						<i class="iconfont">&#xe613;</i>
					</view>
				</view>
			</view>
		</view>
		<uni-load-more :status="loadingType"></uni-load-more>
	</view>
</template>

<script>
	import test_img from '../../static/img4.jpg'
	import uniLoadMore from '../components/uni-load-more/uni-load-more.vue'
	import _ from 'lodash'
	export default {
		data() {
			return {
				pageNum: 1,
				user_info: '',
				recogmentList: [],
				test_img: test_img,
				loadingType: 'more',
				more_page: 0
			}
		},
		components:{
			uniLoadMore
		},
		onLoad() {
			this.$http
			.post('/api/getuser', 
			{
				userid: uni.getStorageSync('userId')
			}, {})
			.then(res => {
				this.user_info = res.data
			})
			.catch(err => {
			})
			this.init()
		},
		onReachBottom(){
			if (this.pageNum <= this.more_page) {
				uni.showLoading({
				    title: '加载中'
				});
				this.init()
			}
		},
		methods: {
			init () {
				uni.showLoading({
				    title: '加载中'
				});
				this.$http
				.post('/api/selectdt', 
				{
					userid: uni.getStorageSync('userId'),
					pageNum: this.pageNum,
					pageSize: 5
				}, {})
				.then(res => {
					console.log(res.data)
					if (res.data.code === 200) {
						_.each(res.data.rows, o => {
							this.recogmentList.push(o)
						})
						uni.hideLoading()
						this.more_page = Math.ceil(res.data.total/5)
						if (this.pageNum === this.more_page) {
							this.loadingType = 'nomore'
						}
						this.pageNum++
					}
					
				})
				.catch(err => {
				})
			}
		}
	}
</script>

